import { defineStore } from 'pinia'
import request from 'utils/request'

export const useUser = defineStore('user', {
  state: () => ({}),
  getters: {},
  actions: {
    async $loginToken(payload) {
      await request({
        url: '/v1/pos/login/token',
        method: 'post',
        data: {
          ...(
            typeof payload !== 'object'
              ? { token: payload }
              : {
                token: payload.token,
                company_token: payload.company
              }
          )
        }
      })
    }
  }
})
