import { defineStore } from 'pinia'
import { useSetting } from '../setting'
import { useUser } from '../user'
import request from 'utils/request'
import store from 'store'
import { computed } from 'vue'

export const useMultipleCompany = defineStore('multipleCompany', {
  state: () => ({
    company: {
      isLoading: false,
      result: null,
      active: null,
      isGhost: null,
    },
    companyDetail: {
      isLoading: false,
      modal: false,
      result: false,
      token: null
    },
    unlink: {
      isLoading: false,
      modal: false
    }
  }),
  getters: {
    companies(state) {
      return computed(() => state?.company?.result ?? [])
    },
    activeCompany(state) {
      return computed(() => state.company?.active?.name ?? 'Default Company')
    },
    isMultipleAccount() {
      return computed(() => !window.location.origin.includes('usedproducts'))
    },
    isGhost(state) {
      return computed(() => state.company.isGhost)
    }
  },
  actions: { 
    generateStatus(company) {
      if (company.subscription_status == 1 && company.trial_end) {
        const dateNow = new Date(Date.now())
        const date = new Date(company.trial_end)
        const diffTime = Math.abs(date - dateNow)
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))

        return ({
          ...company,
          class_alert: 'bg-orange-01',
          class_text: 'text-orange-04',
          class_dot: 'bg-orange-04',
          text: `Trial Ends in`,
          days: diffDays
        })
      }

      if (company.subscription_status == 2) return ({
        ...company,
        class_alert: 'bg-red-01',
        class_text: 'text-red-04',
        class_dot: 'bg-red-04',
        text: 'Unpaid Billing'
      })

      if (company.subscription_status == 3) return ({
        ...company,
        class_alert: 'bg-orange-01',
        class_text: 'text-orange-04',
        class_dot: 'bg-orange-04',
        text: 'Subscribe Cancelled'
      })

      return ({
        ...company,
        class_alert: null,
        class_text: null,
        text: null
      })
    },
    generateList(companies) {
      return companies.filter(company => company.name && company).map(company => ({
        ...this.generateStatus(company)
      }))
    },
    async $getCompanies(force = false) {
      const companyList = store?.state?.user?.me?.companies ?? []
      if (companyList && !force) {
        this.company.result = this.generateList(companyList)
      } else {
        const storeSetting = useSetting()
        await store.dispatch('user/me')
        if (store?.state?.user?.me?.menu_v2) storeSetting.setMenu(store?.state?.user?.me?.menu_v2)
        this.company.result = this.generateList(store?.state?.user?.me?.companies ?? [])
      }
    },
    async $getActiveCompany() {
      const storeSetting = useSetting()
      const activates = storeSetting.company.form ?? undefined
      if (activates) {
        this.company.active = activates
      } else {
        await storeSetting.$getSettingCompanyProfile()
        this.company.active = storeSetting.company.form
      }

    },
    async $getIsGhost() {
      const isGhost = store?.state?.user?.me?.is_ghost ?? 0
      this.company.isGhost = isGhost ? true : false
    },
    async $getBulkMultipleCompany() {
      await this.$getCompanies()
      await this.$getActiveCompany()
      await this.$getIsGhost()
    },
    async $getCompany(payload) {
      const response = await request({
        url: `/v1/config/company/show`,
        method: 'get',
        params: {
          ...payload,
          company_code: this.companyDetail.token
        }
      })
      this.companyDetail.result = response
    },
    async $updateMultipleCompany(payload) {
      this.company.isLoading = true
      this.company.active.name = payload.name
      const selectedIndex = this.company?.result?.findIndex(company => company.is_selected) ?? -1
      if (this.company?.result[selectedIndex]) this.company.result[selectedIndex].name = payload.name
      this.company.isLoading = false
    },
    async $unlinkAccount() {
      if (!this.companyDetail.token) return
      await request({
        url: `/v1/config/company/unlink`,
        method: 'post',
        data: {
          company_code: this.companyDetail.token
        }
      })
    },
    async $switchCompany(payload) {
      const storeUser = useUser()
      await storeUser.$loginToken(payload)
    }
  }
})